import styled from 'styled-components';

const Brand = styled.h1`
	font-size: 20px;
	color: #000000;
	margin: 0;
	padding-left: 10px;
	top: 3px;
	position: relative;
	font-family: 'Permanent Marker', cursive;
	text-transform: uppercase;
`;

export default Brand;


